<template>
  <figure :class="['thumbnail', { 'thumbnail--wide' : size === 'big' } ]">
    <img
      class="thumbnail__img"
      :srcset="srcsetImage"
      :src="srcImage"
      :alt="alt"
      :itemprop="itemprop"
      @load="$emit('load')">
  </figure>
</template>

<script>
export default {
  name: 'ProductCardThumbnail',
  data: () => ({
    observer: null,
    intersected: false,
    loaded: false,
    src: null,
    srcset: null,
  }),
  computed: {
    srcImage() {
      return this.intersected && this.src ? this.src : '//:0'
    },
    srcsetImage() {
      return this.intersected && this.srcset ? this.srcset : false
    }
  },
  created() {
    this.src = require(`@/assets/images/products/${this.name.toLocaleLowerCase().replace(' ', '-')}/${this.size}-1200.jpg`)
    this.srcset = `${require(`@/assets/images/products/${this.name.toLocaleLowerCase().replace(' ', '-')}/${this.size}-400.jpg`)} 600w,
                   ${require(`@/assets/images/products/${this.name.toLocaleLowerCase().replace(' ', '-')}/${this.size}-800.jpg`)} 800w,
                   ${require(`@/assets/images/products/${this.name.toLocaleLowerCase().replace(' ', '-')}/${this.size}-1200.jpg`)} 1200w`
  },
  mounted() {
    if ('IntersectionObserver' in window) {
      this.observer = new IntersectionObserver(entries => {
        const image = entries[0]
        if (image.isIntersecting) {
          this.intersected = true
          this.observer.disconnect()
          this.$emit('intersect')
        }

      }, {
          rootMargin: '150px',
          threshold: 0.1
      })

      this.observer.observe(this.$el)
    }
  },
  destroyed() {
    if ('IntersectionObserver' in window) {
      this.observer.disconnect()
    }
  },
  props: {
    name: {
      type: String,
      required: true
    },
    size: {
      type: String,
      required: true
    },
    alt: {
      type: String,
      default: 'Product Thumbnail'
    },
    itemprop: {
      type: String,
    }
  }
}
</script>

